* {
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  width: 100%;
  min-height: 100vh;
}

h1 {
  margin-bottom: 2.5rem;
}

input {
  font-size: 1.2rem;
  font-weight: bold;
  padding: .3rem;
  text-align: center;
}
select {
  box-sizing: content-box;
 padding: .5rem;
  font-size: 1rem;
}

#input-a, #input-b {
  width: 10rem;
}

#sideA {
 font-size: .9rem;
} 

.container {
  max-width: 100%;
  text-align: center;
  font-weight: 100;
}

.fa-equals {
  padding: 1.5rem;
  font-size: 3rem;
}
